body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.track-play-btn-wrapper {
  padding-left: 5px;
}

.track-play-btn {
  padding: 5px;
  margin: 0;
  width: 25px;
  height: 25px;
  background: #fff;
  border: 2px solid #bbb;
  overflow: hidden;
  cursor: pointer;
  box-sizing: border-box;
}

.track-title {
  overflow: hidden;
  width: 250px; 
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #6f6f6f;
  font-weight: 700;
}

.track-play-icon {
  position: relative;
  left: 2px;
  border: 5px solid transparent;
  border-left: 9px solid #bbb;
  cursor: pointer;
  width: 80px;
  box-sizing: border-box;
}

.track-pause-icon {
  height: 10px;
  width: 10px;
  position: relative;
  top: 1px;
  left: 1px;
  cursor: pointer;
  box-sizing: border-box;
}

.track-pause-icon:before,
.track-pause-icon:after {
  position: absolute;
  top: 0;
  display: block;
  width: 3px;
  height: 100%;
  content: '';
  background: #bbb;
}

.track-pause-icon:before {
  left: 0;
}

.track-pause-icon:after {
  right: 0;
}

.track {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #90bfb3;
  padding: 4px;
}

.track.active .track-title {
  color: #00c6c6;
}

.track.active .track-play-btn {
  border-color: #00c6c6;
}

.track.active .track-play-icon {
  border-left-color: #00c6c6;
}

.track.active .track-pause-icon:before,
.track.active .track-pause-icon:after {
  background: #00c6c6;
}
.progress-slider {
  margin-top: 5px;
  width: 100%;
  height: 4px;
  /*border: 1px solid black;*/
  cursor: pointer;
  transition: all .2s linear;
  padding: 3px 0;
}

.progress-slider:hover .progress-slider_handler {
  opacity: 1;
}

.progress-slider_line {
  width: 100%;
  height: 100%;
  background: #bbb;
  position: relative;
}

.progress-slider_filler {
  background: #4fdede;
  height: 100%;
}

.progress-slider_handler {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #00c6c6;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 0;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  transition: opacity .2s linear;
}
.player {
  margin-top: 10px;
  margin-bottom: 30px;
  width: 100%;
}

.control-btn-wrapper {
  display: flex;
}

.control-btn {
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: 0;
  padding: 0;
  margin: 0;
  height: 30px;
  background: #fff;
  border: none;
  overflow: hidden;
  cursor: pointer;
  box-sizing: border-box;
}

.player-play-icon {
  position: relative;
  left: 50%;
  border: 10px solid transparent;
  border-left: 18px solid #90bfb3;
  cursor: pointer;
  -webkit-transform: translateX(-10%);
          transform: translateX(-10%);
  width: 80px;
  box-sizing: border-box;
}

.player-pause-icon {
  height: 20px;
  width: 20px;
  position: relative;
  top: 1px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  cursor: pointer;
  box-sizing: border-box;
}

.player-pause-icon:before,
.player-pause-icon:after {
  position: absolute;
  top: 0;
  display: block;
  width: 5px;
  height: 100%;
  content: '';
  background: #90bfb3;
}

.player-pause-icon:before {
  left: 0;
}

.player-pause-icon:after {
  right: 0;
}

.player-next-icon,
.player-prev-icon {
  height: 14px;
  width: 20px;
  position: relative;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  cursor: pointer;
  box-sizing: border-box;
}

.player-prev-icon {
  -webkit-transform: rotate(180deg) translateX(50%);
          transform: rotate(180deg) translateX(50%);
}

.player-next-icon:before,
.player-prev-icon:before {
  position: absolute;
  top: 0;
  display: block;
  width: 5px;
  content: '';
  border: 7px solid transparent;
  border-left: 12px solid #90bfb3;
}

.player-next-icon:after,
.player-prev-icon:after {
  position: absolute;
  top: 0;
  display: block;
  width: 5px;
  height: 100%;
  content: '';
  background: #90bfb3;
  right: 0;
}

.track-info-wrapper {
  display: flex;
  color: #90bfb3;
  width: 100%;
  min-width: 0;
}

.track-info-duration {
  margin-left: auto;
  font-size: 14px;
  color: #90bfb3;
}

.track-info-name {
  width: 60%;
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
}
.search {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.search-input {
  flex: 1 1;
  border: 1px solid #90bfb3;
  padding: 3px;
}

.search-btn {
  border: 1px solid #90bfb3;
  border-left: none;
  background: #90bfb3;
  color: #fff;
  cursor: pointer;
}
